agia img {\n  background:#FFFFFF;\n  border:5px solid #FFFFFF;\n  width:100%;\n}\n\n.card-imagia .content-imagia {\n  background-color:rgba(0, 0, 0, 0);\n  box-shadow:none;\n  padding:10px 20px 20px;\n}\n\n.content-back-imagia {\n  display:flex;\n  align-items:center;\n  height:100%;\n}\n\n.card-imagia .name-imagia {\n  font-size:22px;\n  line-height:26px;\n  margin:0;\n  text-align:center;\n  text-transform:capitalize;\n}\n\n.card-imagia .subtitle-imagia {\n  color:#aaaaaa;\n  text-align:center;\n  margin-bottom:15px;\n}\n\n.card-imagia .footer-imagia {\n  width:100%;\n  color:#aaaaaa;\n  padding:10px 0;\n  text-align:center;\n  position:absolute;\n  bottom:0;\n  border-top:1px solid white;\n}\n\n.card-imagia .footer-imagia .social-imagia a {\n  color: white;\n  margin:0 7px;\n  font-size:18px;\n}\n\n.card-imagia .footer-imagia .social-imagia a:hover {\n  color: #aaaaaa;\n}\n\n.card-imagia .footer-imagia .social-imagia a:active {\n  color: white;\n}\n\n@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {\n  .front-imagia, .back-imagia {\n    -webkit-backface-visibility:visible;\n            backface-visibility:visible;\n  }\n}\n\n@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {\n  .back-imagia {\n    visibility:hidden;\n    -ms-transition:all 0.2s cubic-bezier(.92,.01,.83,.67);\n  }\n}\n\n@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {\n  .front-imagia {\n    z-index:4;\n  }\n}\n\n@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {\n  .card-container-imagia:hover .back-imagia {\n    z-index:5;\n    visibility:visible;\n  }\n}\n\n"